function ClubButtons(props) { // rock, paper, and scissor buttons
	if (props.value.includes("Basketball")) {
    return (
      <button class="btn btn-outline-primary mr-4" onClick={props.onClick}>
        {<img src = 'https://goodday451999.github.io/Rock-Paper-Scissors-Neo/images/stone.png' width="200" height="200"/>}
      </button>
    );
  } else if (props.value.includes("FitnessStudio")) {
     return (
      <button class="btn btn-outline-primary mr-4" onClick={props.onClick}>
        {<img src = 'https://hacktoberfest.lingonsaft.com/images/paper.png' width="200" height="200"/>}
      </button>
     );
  } else if (props.value.includes("Track")) {
    return (
      <button class="btn btn-outline-primary" onClick={props.onClick}>
        {<img src = 'https://hacktoberfest.lingonsaft.com/images/scissors.png' width="200" height="200"/>}
      </button>
    );
  }
}


